
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import myReportService from "service/myReportService";
import {setTimeout} from "timers";
import loading from "components/common/loading";

import {toFixed, accAdd, accMul} from 'plugins/calc'

const list = () => ["本日", "本周", "本月", "本年"];
export default {
components: {
Tab,
TabItem,
Divider,
loading,
SwiperItem,
FormPreview,
CellFormPreview
},
data() {
return {
listTab: list(),
demo2: "本日",
list1: [],
list2: [],
list3: [],
list1Total: 0,
list2Total: 0,
list3Total: 0,
remark: "",
spinner: false,
totalCoverNum: 0, // 销量套数
costSalesRatio: '', // 费用销售比
};
},
filters: {
numberComma
},
methods: {
mylist(tab) {
this.totalCoverNum = 0;
this.costSalesRatio = '';
if (tab == 0) {
this.listpanl(this.remark.days);
} else if (tab == 1) {
this.listpanl(this.remark.weeks);
} else if (tab == 2) {
this.listpanl(this.remark.months);
} else if (tab == 3) {
this.listpanl(this.remark.years);
}
},
listData() {
let ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));
let jsonData = {




shengName: '',
bankName: '',
sybName: '',
bmName: '',
};
myReportService
.myRepotList(jsonData)
.then(data => {
if (data) {
this.spinner = false;
}
this.remark = data;
this.mylist(0);
})
.catch(data => {
this.spinner = true;
});
},
listpanl(m) {
this.list1Total = 0;
this.list2Total = 0;
this.list3Total = 0;
this.list1.length = 0;
this.list2.length = 0;

for (let i = 0; i < m.length; i++) {
let item = m[i];
if (item.objType == "项目类产品" && item.objName) {
this.list1Total += Number(item.amount);
this.list1.push({
label: item.objName,

value: `￥${numberComma(item.amount, 3)} (${item.qty}件/折合${toFixed(item.coverNum || 0)}套)`,
});
this.totalCoverNum = accAdd(this.totalCoverNum, item.coverNum);
} else if (item.objType == "A") {
this.list2Total = item.amount;
this.list2 = [
{
label: '费用明细',
value: '',
}, {
label: '住宿费',
value: item.hotelCost ? `￥${numberComma(toFixed(Number(item.hotelCost)))}` : '无',
}, {
label: '市内交通费',
value: item.cityTrafficCost ? `￥${numberComma(toFixed(Number(item.cityTrafficCost)))}` : '无',
}, {
label: '长途交通费',
value: item.longCityTrafficCost ? `￥${numberComma(toFixed(Number(item.longCityTrafficCost)))}` : '无',
}, {
label: '其他费用',
value: item.elseCost ? `￥${numberComma(toFixed(Number(item.elseCost)))}` : '无',
}, {
label: '合计',
value: item.hotelAndElseCost ? `￥${numberComma(toFixed(Number(item.hotelAndElseCost)))}` : '无',
},
]
this.costSalesRatio = `${toFixed(accMul(item.hotelAndElseCost / item.amount, 100))}%`;
} else if (item.objType == "B") {
this.list3Total = item.amount;
}
}
this.list1Total = this.list1Total.toFixed(2);
}
},
created() {
this.spinner = true;
this.listData();
}
};



